.developer {
  @media (min-width: $tablet-width) {
    display: flex;
  }
}

.developer__text {
  display: inline-block;
  margin-right: 11px;

  @media (min-width: $tablet-width) {
    margin-left: auto;
  }
}

.developer__link {
  &:hover .developer__icon {
    fill: $main-color;
  }

  &:active .developer__icon {
    opacity: 0.3;
  }
}
